@import url(https://fonts.googleapis.com/css2?family=Esteban&family=Montserrat:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Esteban&family=Montserrat:wght@900&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  /*font-family: 'Signika', sans-serif;*/
  font-family: 'Esteban', serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content-wrapper {
  overflow: auto;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
}

.greeting-wrapper {
    height: 100vh;
    background-image: url(/portfolio/static/media/background-dark.62f0eda8.png);
}

.greeting {
    width: 25%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
    width: 100%;
    padding-top: 100px
}

.greeting img {
    width: 200px;
    height: auto;
    border-radius: 50%;
}

@media screen and (max-width: 400px) {
    .greeting img {
        display: none; 
    }
}

.greeting p {
    text-align: center;
    color: white;
    font-size: larger;
    border-radius: 15px;
    width: 30%;
}
.titlebar {
    display: flex;
    font-family: 'Montserrat', sans-serif;
    justify-content: space-between;
    padding-right: 75px;
    position: fixed;
    width: 100%;
    height: 120px;
}

.icon-wrapper {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    padding-left: 10px;
    padding-right: 10px;
}

.icon-wrapper:hover {
    background-color: #465362;
}

.icons {
    display: flex;
    justify-content: center;
    height: 100%;
}

.icons img {
    height: 40px;
    width: auto;
}

.links {
    align-self: center;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.section-link {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    padding-left: 5px;
    padding-right: 5px;
}

.section-link a {
    color: white;
    text-decoration: none;
    font-size: x-large;
    padding-left: 10px;
    padding-right: 10px;
}

.section-link:hover{
    background-color: #465362;
}
.projects-wrapper {
    background-color: white;
    scroll-snap-align: start;
    min-height: 100vh;
    background-color: #011936;
}

.project-list {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 120px;
}
.project-card {
    background-color: white;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 400px;
    width: 400px;
    margin: 15px;
    padding: 5px;
}

.project-description{
    text-align: center;
}

.github-link {
    border-radius: 50%;
}

.github-link:hover {
    background-color: #465362;
}
.contact {
    height: 100vh;
    background-color: #465362;
    display: flex;
    justify-content: center;
}

.contact-form-wrapper {
    display: flex;
    justify-content: center;
    margin-top: 120px;
    background-color: white;
    width: 300px;
    height: 300px;
    border-radius: 5%;
}

form {
    display: flex;
    flex-direction: column;
}

#submit-button {
    margin-top: 30px;
}
